<template>
  <div class="row">
    <div v-show="isActive === 'video'" class="col-12">
      <div  class="card bg-transparent shadow-none border-0 mb-0">
        <lesson-video v-if="lessonData.video"
                      class="card-img"
                      :video="lessonData.video"
                      :title="lessonData.title"/>
        <div v-if="hasFinished" class="card-img-overlay d-flex align-items-center bg-translucent-dark">
          <div class="my-auto col-12">
            <div class="row text-center">
              <div class="col-12 col-xl-4 mb-2 mx-auto">
                <base-button type="secondary" @click.prevent="setVideo()" block><i class="fas fa-redo"></i> Repeat {{ capitalize(this.isActive) }}</base-button>
              </div>
              <div v-if="lessonData.mp3_dialogue" v-show="isActive === 'video'"  class="col-12 col-xl-4 mb-2 mx-auto">
                <base-button type="secondary" @click.prevent="setAudio('dialogue')" block><i class="ni icon-speaking"></i> Lesson Dialogue</base-button>
              </div>
              <div v-if="lessonData.mp3_thefix" v-show="isActive === 'video' && !lessonData.mp3_dialogue"  class="col-12 col-xl-4 mb-2 mx-auto">
                <base-button type="secondary" @click.prevent="setAudio('vocabulary')" block><i class="ni icon-vocabulary"></i> Lesson Review</base-button>
              </div>
              <div v-if="lessonData.mp3_thefix" v-show="isActive === 'dialogue'" class="col-12 col-xl-4 mb-2 mx-auto">
                <base-button type="secondary" @click.prevent="setAudio('vocabulary')" block><i class="ni icon-vocabulary"></i> Lesson Review</base-button>
              </div>
              <div v-if="lessonData.mp3_private" v-show="isActive === 'vocabulary'" class="col-12 col-xl-4 mb-2 mx-auto">
                <base-button type="secondary"   @click.prevent="setAudio('lesson')" block><i class="fas fa-podcast"></i> Back To Lesson</base-button>
              </div>
              <div v-if="nextItem" class="col-12 col-xl-4 mb-2 mx-auto">
                <base-button type="success"  :loading="navigating" @click.prevent="navigateToNext" block>To Next Lesson <i class="fas fa-step-forward"></i></base-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-show="isActive !== 'video'" class="col-12">
      <div class="card mb-0">
        <img
          class="card-img"
          v-lazy="lessonImg"
          :alt="lessonData.title"
        >
        <div class="card-img-overlay d-flex align-items-center" :class="hasFinished ? 'bg-translucent-dark' : ''">
          <div v-show="!hasFinished" class="mx-auto">
            <div class="audio-player-controls">
              <div class="col-12">
                <div class="row">
                  <div class="col-4">
                    <base-button class="bg-transparent border-0 btn-icon-only m-md-3 m-lg-5 shadow-none"><i v-show="!loading" class="fi flaticon-040-backward text-white jump emphasize-dark" @click.prevent="jumpBack"></i></base-button>
                  </div>
                  <div class="col-4">
                    <base-button class="bg-transparent border-0 btn-icon-only m-md-3 m-lg-5 shadow-none"><i class="text-white play-button emphasize-dark" :class="[ loading ? 'fa fa-spinner fa-spin' : isPlaying ? 'fi flaticon-021-pause' : 'fi flaticon-013-play' ]" @click.prevent="togglePlayback()"></i></base-button>
                  </div>
                  <div class="col-4">
                    <base-button class="bg-transparent border-0 btn-icon-only m-md-3 m-lg-5 shadow-none"><i v-show="!loading" class="fi flaticon-032-forwards text-white jump emphasize-dark" @click.prevent="jumpAhead"></i></base-button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div v-show="hasFinished" class="my-auto col-12">
            <div class="row text-center">
              <div class="col-12 col-xl-4 mb-2 mx-auto">
                <base-button type="secondary" @click.prevent="setAudio(isActive)" block><i class="fas fa-redo"></i> Repeat {{ capitalize(this.isActive) }}</base-button>
              </div>
              <div v-if="lessonData.mp3_dialogue"  v-show="isActive === 'lesson'"  class="col-12 col-xl-4 mb-2 mx-auto">
                <base-button type="secondary" @click.prevent="setAudio('dialogue')" block><i class="ni icon-speaking"></i> Lesson Dialogue</base-button>
              </div>
              <div v-if="lessonData.mp3_thefix" v-show="isActive === 'lesson' && !lessonData.mp3_dialogue"  class="col-12 col-xl-4 mb-2 mx-auto">
                <base-button type="secondary" @click.prevent="setAudio('vocabulary')" block><i class="ni icon-vocabulary"></i> Lesson Review</base-button>
              </div>
              <div v-if="lessonData.mp3_thefix" v-show="isActive === 'dialogue'" class="col-12 col-xl-4 mb-2 mx-auto">
                <base-button type="secondary" @click.prevent="setAudio('vocabulary')" block><i class="ni icon-vocabulary"></i> Lesson Review</base-button>
              </div>
              <div v-if="lessonData.video"  v-show="isActive === 'vocabulary'" class="col-12 col-xl-4 mb-2 mx-auto">
                <base-button type="secondary"   @click.prevent="setVideo()" block><i class="fas fa-film"></i> Back To Video</base-button>
              </div>
              <div v-else-if="lessonData.mp3_private"  v-show="isActive === 'vocabulary'" class="col-12 col-xl-4 mb-2 mx-auto">
                <base-button type="secondary"   @click.prevent="setAudio('lesson')" block><i class="fas fa-podcast"></i> Back To Lesson</base-button>
              </div>
              <div v-if="nextItem" class="col-12 col-xl-4 mb-2 mx-auto">
                <base-button type="success"  :loading="navigating" @click.prevent="navigateToNext" block>To Next Lesson <i class="fas fa-step-forward"></i></base-button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <vue-plyr ref="plyr" :options="playerOptions">
        <audio controls>
          <source :src="audioSource" type="audio/mp3"/>
        </audio>
      </vue-plyr>
    </div>
    <div class="col-12 mt-2">
      <div class="row">
        <div class="col-12">
          <div class="row">
            <div class="col-auto">
              <base-switch :value="autoMarkStudied" @input="toggleAutoMarkStudied($event)"></base-switch>
            </div>
            <div class="col small pt-1">Automatically mark lessons as completed
              <el-popover placement="top" trigger="hover" width="240" title="Automatically Complete">
                <i class="fa fa-info-circle" slot="reference"></i>
                <span class="text-left">We'll mark lessons as completed once you cross the 95% mark. Don't worry, they'll always be available in your <router-link class="btn-link" to="/history">History</router-link> area.</span>
              </el-popover>
            </div>
          </div>
        </div>
        <div v-if="lessonData.video" class="col-6 col-md-4 mt-2">
          <base-button @click.prevent="setVideo" :type="isActive === 'video' ? 'primary' : 'secondary'" class="btn-block"><i class="fas fa-film"></i> Video Lesson</base-button>
        </div>
        <div v-if="lessonData.mp3_private" class="col-6 col-md-4 mt-2">
          <base-button @click.prevent="setAudio('lesson')" :type="isActive === 'lesson' ? 'primary' : 'secondary'" class="btn-block"><i class="fas fa-podcast"></i> Audio Lesson</base-button>
        </div>
        <div  v-if="lessonData.mp3_dialogue" class="col-6 col-md-4 mt-2">
          <base-button @click.prevent="setAudio('dialogue')" :type="isActive === 'dialogue' ? 'primary' : 'secondary'" class="btn-block"><i class="ni icon-speaking"></i> Dialogue</base-button>
        </div>
        <div v-if="lessonData.mp3_thefix" class="col-6 col-md-4 mt-2">
          <base-button @click.prevent="setAudio('vocabulary')" :type="isActive === 'vocabulary' ? 'primary' : 'secondary'" class="btn-block"><i class="ni icon-vocabulary"></i> Review</base-button>
        </div>
      </div>
    </div>
    <div class="d-none d-md-block">
      <div class="fixed-bottom col-3 offset-9 col-xl-2 offset-xl-10 mb-5">
        <div v-show="!showFloatingControls">
          <base-button type="dark" icon @click="showFloatingControls = !showFloatingControls" class="float-right btn-icon-only"><i class="text-white floating-player-icon" :class="[ loading ? 'fa fa-spinner fa-spin' : isPlaying ? 'fi flaticon-021-pause' : 'fi flaticon-013-play' ]"></i></base-button>
        </div>
        <div v-show="showFloatingControls" class="card bg-translucent-dark text-white border-0 audio-player-float mb-0">
          <div class=" d-flex align-items-center">
            <div class="col-12">
              <div class="mx-auto">
                <div class="row text-center">
                  <div class="col-12">
                    <base-button @click="showFloatingControls = !showFloatingControls" class="float-right btn-icon-only bg-transparent border-0 shadow-none"><i class="fa fa-times"></i></base-button>
                  </div>
                  <div class="col-12">
                    <div class="row">
                      <div class="col-4">
                        <base-button class="bg-transparent border-0 shadow-none p-0 m-0" @click.prevent="floatingJumpBack()"><i v-show="!loading" class="fi flaticon-040-backward text-white emphasize-dark jump" ></i></base-button>
                      </div>
                      <div class="col-4">
                        <base-button class="bg-transparent border-0 shadow-none p-0 m-0" @click.prevent="floatingTogglePlayback()"><i class="text-white emphasize-dark play-button" :class="[ loading ? 'fa fa-spinner fa-spin' : isPlaying ? 'fi flaticon-021-pause' : 'fi flaticon-013-play' ]"></i></base-button>
                      </div>
                      <div class="col-4">
                        <base-button class="bg-transparent border-0 shadow-none p-0 m-0"  @click.prevent="floatingJumpAhead()"><i v-show="!loading" class="fi flaticon-032-forwards text-white emphasize-dark jump"></i></base-button>
                      </div>
                    </div>
                  </div>
                  <div class="col-12">
                    <base-progress show-label :label="`${lessonData.title} - ${isActive}`" type="white" :height="floatingProgressWidth" :value="floatingProgressBar"></base-progress>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import qingWen from '@/assets/img/qing-wen.jpg'
  import {mapState, mapMutations} from 'vuex'
  import toCapitalize from 'lodash.capitalize'

  import LessonVideo from "./Video";

  export default {
    name: "LessonMedia",
    components: {LessonVideo},
    title () { return `${this.lessonData.title} | ChinesePod`},
    computed: {
      floatingProgressBar () {
        return this.floatingProgress && this.floatingProgress > 0 ? this.floatingProgress : 0
      },
      audioSource () {
        return process.env.VUE_APP_ROOT_S3 + (this.lessonData.type === 'extra' ? '/extra/' : '/') + this.lessonData.id + '/' + this.lessonData.hash_code + '/' + this.lessonData['mp3_private']
      },
      player () { return this.$refs.plyr.player },
      lessonImg () {
        let imgSlug = this.lessonData.image;
        if (imgSlug.slice(0,4) === 'http') {
          return imgSlug
        } else {
          return this.lessonData.id[0] === 'Q'
            ? `${this.qingWen}`
            : `https://s3contents.chinesepod.com${this.lessonData.type === 'extra' ? '/extra/' : '/'}${this.lessonData.id}/${this.lessonData.hash_code}/${this.lessonData.image}`
        }
      },
      ...mapState({
        autoMarkStudied: state => state.userInfo.autoMarkStudied
      })
    },
    props: {
      lessonData: Object,
      nextItem: Boolean
    },
    methods: {
      async setAudio (input) {
        if(this.loading) {
          return
        } else {

          this.loading = true;
          switch (input) {
            default:
              this.isActive = input;
              this.player.source = {
                type: 'audio',
                title: 'Audio Lesson',
                sources: [
                  {
                    src: process.env.VUE_APP_ROOT_S3 + (this.lessonData.type === 'extra' ? '/extra/' : '/') + this.lessonData.id + '/' + this.lessonData.hash_code + '/' + this.lessonData['mp3_private'],
                    type: 'audio/mp3'
                  }
                ]
              };
              this.$gtag.event('Play', {
                'event_category': 'Audio',
                'event_label': this.lessonData.title
              });
              break;

            case 'dialogue':
              this.isActive = input;
              this.player.source = {
                type: 'audio',
                title: 'Audio Dialogue',
                sources: [
                  {
                    src: process.env.VUE_APP_ROOT_S3 + (this.lessonData.type === 'extra' ? '/extra/' : '/') + this.lessonData.id + '/' + this.lessonData.hash_code + '/' + this.lessonData['mp3_dialogue'],
                    type: 'audio/mp3'
                  }
                ]
              };
              break;

            case 'vocabulary':
              this.isActive = input;
              this.player.source = {
                type: 'audio',
                title: 'Audio Review',
                sources: [
                  {
                    src: process.env.VUE_APP_ROOT_S3 + (this.lessonData.type === 'extra' ? '/extra/' : '/') + this.lessonData.id + '/' + this.lessonData.hash_code + '/' + this.lessonData['mp3_thefix'],
                    type: 'audio/mp3'
                  }
                ]
              };
              break;
          }

          this.hasFinished = false;
          await this.player.play();

          this.$gtag.event('select_content', {
            'event_category': 'engagement',
            'event_label': input
          });

        }

      },
      setVideo () {
        this.hasFinished = false;
        this.player.pause();
        this.isActive = 'video';

        this.videoPlayer.play();

        this.$gtag.event('select_content', {
          'event_category': 'engagement',
          'event_label': 'video'
        });

      },
      jumpAhead () {
        this.player.forward();
        // this.player.togglePlay()
      },
      jumpBack () {
        this.player.rewind();
        // this.player.togglePlay()
      },
      async togglePlayback () {
        if (!this.loadingSilent) {
          await this.player.togglePlay(!this.player.playing)
        }

      },
      async logProgress (current) {
        if (current === 100) {
          this.hasFinished = true;
          this.$emit('finished');
        }

        if (this[`${this.isActive}Progress`] < current) {

          if (!this.sendingProgress) {
            this.sendingProgress = true;
            this[`${this.isActive}Progress`] = current;
            await this.$http.post(process.env.VUE_APP_ROOT_API + '/api/v1/lessons/progress', {
              lessonId: this.lessonData.id,
              track: this.isActive,
              progress: current
            }).catch(() => {});
            if(this.isActive !== 'video') {
              this.$gtag.event(`${current}% Listened - ${this.isActive}`, {
                'event_category': 'Audio',
                'event_label': this.lessonData.title
              });
            }
            this.sendingProgress = false;
          }

          if (this.autoMarkStudied && current > 90 && ['lesson', 'video'].includes(this.isActive)) {
            this.$emit('studied');
          }
        }
      },
      navigateToNext () {
        this.navigating = true;
        this.$emit('proceed');
      },
      capitalize (string) {
        return toCapitalize(string)
      },
      floatingJumpBack() {
        if (this.isActive === 'video') {
          this.videoPlayer.time(this.videoPlayer.time() - 10);
        } else {
          this.player.rewind();
        }
      },
      floatingJumpAhead() {
        if (this.isActive === 'video') {
          this.videoPlayer.time(this.videoPlayer.time() + 10);
        } else {
          this.player.forward();
        }
      },
      async floatingTogglePlayback () {
        if (this.isActive === 'video') {
          if (this.videoPlayer.state() === "playing") {
            this.videoPlayer.pause()
          } else {
            this.videoPlayer.play()
          }
        } else {
          await this.togglePlayback();
        }
      },
      ...mapMutations({
        toggleAutoMarkStudied: 'TOGGLE_AUTO_MARK_STUDIED'
      })
    },
    data () {
      return {
        playerOptions: {
          controls: ['play-large', 'play', 'progress', 'current-time', 'mute', 'volume', 'captions', 'settings', 'pip', 'airplay', 'fullscreen', 'controls'],
          settings: ['captions', 'quality', 'speed', 'loop', 'controls'],
          loop: {active: false}
        },
        videoPlayer: '',
        isActive: 'lesson',
        isPlaying: false,
        qingWen: qingWen,
        loading: false,
        loadingSilent: false,
        maxProgress: 0,
        videoProgress: -1,
        lessonProgress: -1,
        vocabularyProgress: -1,
        dialogueProgress: -1,
        historicalProgress: 0,
        sendingProgress: false,
        hasFinished: false,
        navigating: false,
        floatingProgress: 0,
        floatingProgressWidth: 8,
        showFloatingControls: false
      }
    },
    mounted() {
      if (this.lessonData && this.lessonData.video) {
        let wistiaScript = document.createElement('script');
        wistiaScript.setAttribute('src', 'https://fast.wistia.net/assets/external/E-v1.js');
        document.head.appendChild(wistiaScript);
        this.isActive = 'video';
        window._wq = window._wq || [];
        _wq.push({ id: this.lessonData.video, onReady: (video) => {
            this.videoPlayer = video;
            video.bind('timechange', async (t) => {
              let current = Math.floor(t / video.duration() * 100 / 5) * 5;
              this.floatingProgress = Math.round(t / video.duration() * 100);
              await this.logProgress(current);
            });
            video.bind('play', () => {
              if (this.hasFinished) {
                this.hasFinished = false;
              }
              this.isPlaying = true;
            });
            video.bind('pause', () => {
              this.isPlaying = false;
            });
            video.bind('end', () => {
              this.hasFinished = true;
            })
          }});
      }
      this.player.on('playing', () => {
        this.loading = false;
        if (this.hasFinished) {
          this.hasFinished = false;
        }
        this.isPlaying = true;

        if ('mediaSession' in navigator) {
          navigator.mediaSession.metadata = new MediaMetadata({
            title: this.lessonData.title,
            artist: 'ChinesePod',
            album: `${this.lessonData.level} Lesson`,
            artwork: [
              { src: 'https://www.chinesepod.com/home/img/icons/icon_64.png',  sizes: '64x64',   type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_64.png',  sizes: '96x96',   type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_120.png', sizes: '120x120', type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_120.png', sizes: '128x128', type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_192.png', sizes: '192x192', type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_192.png', sizes: '256x256', type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_384.png', sizes: '384x384', type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_512.png', sizes: '512x512', type: 'image/png' }
            ]
          });
          navigator.mediaSession.playbackState = 'playing';
          navigator.mediaSession.setActionHandler('play', () => this.player.togglePlay());
          navigator.mediaSession.setActionHandler('pause', () => this.player.togglePlay());
          navigator.mediaSession.setActionHandler('seekbackward', () => this.player.rewind());
          navigator.mediaSession.setActionHandler('seekforward', () => this.player.forward());
          // navigator.mediaSession.setActionHandler('previoustrack', function() {});
          // navigator.mediaSession.setActionHandler('nexttrack', function() {});
        }
      });
      this.player.on('pause', () => {
        this.isPlaying = false;
        if ('mediaSession' in navigator) {
          navigator.mediaSession.playbackState = "paused";
        }
      });
      this.player.on('timeupdate', async () => {
        let current = Math.floor((this.player.currentTime / this.player.duration) * 100 / 5) * 5;
        this.floatingProgress = Math.round((this.player.currentTime / this.player.duration) * 100);
        await this.logProgress(current);
      });
      this.player.on('seeking', () => {
        this.loadingSilent = true;
      });
      this.player.on('seeked', () => {
        this.loadingSilent = false;
      });
    }
  }
</script>

<style lang="scss">
  @import '~plyr/dist/plyr.css';
