      }
      @media(min-width: 768px) {
        font-size: 5rem;
      }
      @media(min-width: 1000px) {
        font-size: 8rem;
      }
    }
  }
  .audio-player-float-wrapper {
    z-index: -1000 !important;
  }
  .progress-label > span {
    /*display: none;*/
    color: white !important;
  }
  .audio-player-float {
    z-index: 1000 !important;
    .play-button, .replay-button, .jump  {
      font-size: 1.2rem;
      /*@media(min-width: 400px) {*/
      /*  font-size: 3.5rem;*/
      /*}*/
      /*@media(min-width: 768px) {*/
      /*  font-size: 5rem;*/
      /*}*/
      @media(min-width: 1000px) {
        font-size: 2.5rem;
      }
    }
  }
  .floating-player-icon {
    font-size: 1.2rem;
  }
